// Material UI
import { makeStyles } from '@mui/styles';
import FormControl from '@mui/material/FormControl';
import FormGroup from '@mui/material/FormGroup';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';

// External
import { useTranslation } from 'react-i18next';

// Owned
import Preference from './Preference';

const useStyles = makeStyles((theme) => ({
  cookieContent: {
    margin: theme.spacing(2)
  },
  paper: {
    margin: theme.spacing(1),
    maxWidth: '900px'
  },
  preferencesForm: {
    width: '100%'
  }
}));

function CookiePreferencesView(props) {
  const classes = useStyles();
  const { t } = useTranslation();

  return (
      <div>
        <FormControl component="fieldset" className={classes.preferencesForm}>
          <FormGroup>
            <Preference 
              title={t('options.preference_selector.advertising.title')}
              name="advertising"
              placeholder={t('options.preference_selector.advertising.placeholder')}
              description={t('options.preference_selector.advertising.description')}
              checked={props.preferences.advertising}
              handleChange={props.handleChange}
              label={props.preferences.advertising ? t('general.accept') : t('general.reject')}
            />
            <Preference 
              title={t('options.preference_selector.functional.title')}
              name="functional"
              placeholder={t('options.preference_selector.functional.placeholder')}
              description={t('options.preference_selector.functional.description')}
              checked={props.preferences.functional}
              handleChange={props.handleChange}
              label={props.preferences.functional ? t('general.accept') : t('general.reject')}
            />
            <Preference 
              title={t('options.preference_selector.performance.title')}
              name="performance"
              placeholder={t('options.preference_selector.performance.placeholder')}
              description={t('options.preference_selector.performance.description')}
              checked={props.preferences.performance}
              handleChange={props.handleChange}
              label={props.preferences.performance ? t('general.accept') : t('general.reject')}
            />
            <Preference 
              title={t('options.preference_selector.other.title')}
              name="other"
              placeholder={t('options.preference_selector.other.placeholder')}
              description={t('options.preference_selector.other.description')}
              checked={props.preferences.other}
              handleChange={props.handleChange}
              label={props.preferences.other ? t('general.accept') : t('general.reject')}
            />
          </FormGroup>
        </FormControl>
        <div>
          <Snackbar open={props.preferencesSaved} autoHideDuration={2000} onClose={() => { props.setPreferencesSaved(false); }}>
              <Alert severity="success">{t('general.saved_preferences')}</Alert>
          </Snackbar>
        </div>
      </div>
  )
}

export default CookiePreferencesView;
