// Material UI
import { makeStyles } from '@mui/styles';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import FormControl from '@mui/material/FormControl';
import FormGroup from '@mui/material/FormGroup';
import FormHelperText from '@mui/material/FormHelperText';
import TextField from '@mui/material/TextField';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';

// Owned
import { Request, Messages } from '../../../../shared';

// External
import { useState } from 'react';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
    paper: {
        margin: theme.spacing(1),
        minHeight: 200
    },
    paperContent: {
        margin: theme.spacing(2),
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        textAlign: 'center'
    },
}));


function PasswordPrompt(props) {
  const classes = useStyles();
  const { t } = useTranslation();
  
  const [password, setPassword] = useState( {
      password: "",
      confirmPassword: ""
  } );

  const [error, setError] = useState("");

  const handleChange = (event) => {
      setPassword(event.target.value);
  }

  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
        await new Request(Messages.KeyManager.GenerateDEK, { password } ).sendRequest();
        props.setHaveDEK(true);
    } catch(e) {
        setError(t('shared.password_prompt.double_check_password'));
        props.setHaveDEK(false);
    }
  }

  return(
    <Paper elevation={3} className={classes.paper}>
        <Container className={classes.paperContent}>
            <div className={classes.paperContent}>
                <form onSubmit={handleSubmit}>
                    <FormControl component="fieldset">
                        <FormGroup>
                            <FormHelperText>
                                <Typography variant="body1">
                                    {t('options.dashboard.password_prompt.title')}
                                </Typography>
                            </FormHelperText>
                            <TextField
                                    id="password"
                                    name="password"
                                    label="Password"
                                    type="password"
                                    value={password["password"]}
                                    onChange={handleChange}
                            />
                            <br />
                            <Button variant="contained" color="primary" style={{marginTop: 30}} type="submit">
                                {t('options.dashboard.password_prompt.action_button')}
                            </Button>
                        </FormGroup>
                    </FormControl>
                </form>
            </div>
            <div>
                <Snackbar open={error} autoHideDuration={2000} onClose={() => { setError(null); }}>
                    <Alert severity="error">{error}</Alert>
                </Snackbar>
            </div>
        </Container>
      </Paper>
  );
}

export default PasswordPrompt;
