import React from 'react';
import { useTable } from 'react-table';

import MaUTable from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'

import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';

import { makeStyles } from '@mui/styles';

import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  info: {
    height: '40px',
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    marginTop: '30px'
  },
  title: {
    flex: '1 1 100%'
  },
  tableRow: {
    paddingTop: '8px',
    paddingBottom: '8px',
    [theme.breakpoints.down('xs')]: {
      padding: '8px'
    }
  },
  descriptionText: {
    color: '#999999'
  },
  desktopTable: {
    display: 'table'
  },
  mobileTable: {
    display: 'none'
  },
  [theme.breakpoints.down('sm')]: {
    desktopTable: {
      display: 'none'
    },
    mobileTable: {
      display: 'table',
      marginTop: '10px'
    }
  }
}));

function Table({ columns, data }) {
  // Use the state and functions returned from useTable to build your UI
  const { getTableProps, headerGroups, rows, prepareRow } = useTable({ columns, data })

  const classes = useStyles();

  // Render the UI for your table
  return (
    <div>
      <MaUTable {...getTableProps()} className={classes.desktopTable}>
        <TableHead>
          {headerGroups.map(headerGroup => (
            <TableRow {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map(column => (
                <TableCell {...column.getHeaderProps()} className={classes.tableRow}>
                  {column.render('Header')}
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableHead>
        <TableBody>
          {rows.map((row, i) => {
            prepareRow(row)
            return (
              <TableRow {...row.getRowProps()}>
                {row.cells.map(cell => {
                  return (
                    <TableCell {...cell.getCellProps()} className={classes.tableRow}>
                      {cell.render('Cell')}
                    </TableCell>
                  )
                })}
              </TableRow>
            )
          })}
        </TableBody>
      </MaUTable>

      <MaUTable {...getTableProps()} className={classes.mobileTable}>
        <TableBody>
          {rows.map((row, i) => {
            prepareRow(row)
            return (
              <TableRow {...row.getRowProps()}>
                <TableCell>
                  <span className={classes.descriptionText}>{row.values["date"]}</span><br></br>
                  <span>{row.values["hostname"]} <br></br> </span>
                  <span className={classes.descriptionText}>{row.values["state"]}</span>
                </TableCell>
              </TableRow>
            )
          })}
        </TableBody>
      </MaUTable>
    </div>
  )
}

function ReportsTable(props) {

  const { t } = useTranslation();

  const classes = useStyles();

  const memoizedData = React.useMemo(() => props.data, [props.data])

  const columns = React.useCallback((t) =>
    [
      {
        Header: t('options.reports.table.header.hostname'),
        accessor: "hostname"
      },
      {
        Header: t('options.reports.table.header.date'),
        accessor: "date"
      },
      {
        Header: t('options.reports.table.header.state'),
        accessor: "state"
      }
    ], []
  );

  if (props.data.length > 0) {
    return (
      <div className={classes.root}>
        <Typography className={classes.title} variant="h6" id="tableTitle" component="div">
          {t('options.reports.title')}
        </Typography>
        <Table
          columns={columns(t)}
          data={memoizedData}
        />
        <div className={classes.info}>
          <Grid container spacing={1} justify="center" direction="row">
            <Grid item>
              <Typography variant="caption">
                {t('options.reports.thank_you')}
              </Typography>
            </Grid>
          </Grid>
        </div>
      </div>
    )
  } else {
    return (
      <div className={classes.root}>
        <div className={classes.info}>
          <Grid container spacing={1} justify="center" direction="row">
            <Grid item>
              <InfoOutlinedIcon fontSize="small" />
            </Grid>
            <Grid item>
              <Typography variant="caption">
                {t('options.reports.no_reports.text1')} <br /> {t('options.reports.no_reports.text2')}
              </Typography>
            </Grid>
          </Grid>
        </div>
      </div>
    )
  }
}

export default ReportsTable;
