import AppBar from '@mui/material/AppBar';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import Hidden from '@mui/material/Hidden';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { CookiePreferences, BehaviorSettings } from './Preferences/Preferences';
import Account from './Account/Account';
import Dashboard from './Dashboard/Dashboard';
import Reports from './Reports/Reports'
import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';

// Styles
import { makeStyles } from '@mui/styles';
import { useTheme } from '@mui/material/styles';

// Icons
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';


import { Request, Messages } from '../../../shared';
import Logo from './../Img/logo.png';
import Menu from './Menu';
import { RatingHelper } from '../../../shared/RatingHelper';
import NoInternetConnection from '../../Shared/View/NoInternetConnection';

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import React, { useState } from 'react';
import WhiteList from './WhiteList/WhiteList';

const drawerWidth = 280;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    appBar: {
        [theme.breakpoints.up('sm')]: {
            width: '100%',
            marginLeft: drawerWidth,
        },
    },
    menuButton: {
        marginRight: theme.spacing(2),
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        },
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
        width: drawerWidth,
    },
    content: {
        flexGrow: 1,
        padding: "0px !important",
        paddingTop: theme.spacing(0),
        marginBottom: '70px',
        marginTop: '0px',
        [theme.breakpoints.up('sm')]: {
            marginTop: '30px'
        }
    },
    toolbarButtons: {
        marginLeft: 'auto',
    },
    logoImage: {
        height: '30px',
        [theme.breakpoints.down('xs')]: {
            height: '18px'
        },
    },
    logoButton: {
        [theme.breakpoints.down('xs')]: {
            paddingLeft: '0px'
        },
    },
    mainGrid: {
        padding: theme.spacing(1),
        [theme.breakpoints.up('sm')]: {
            padding: theme.spacing(2)
        },
    },
    bottomAppBar: {
        top: 'auto',
        bottom: 0,
        alignItems: 'center',
        [theme.breakpoints.down('sm')]: {
            display: 'none'
        }
    },
    rating: {
        marginLeft: 15
    },
    rightNavigationButton: {
        height: '40px',
        '-webkit-text-size-adjust': '70%',
        whiteSpace: 'nowrap'
    }
}));

const onRatingClick = (event) => {
    let storeURL = RatingHelper.storeURL(navigator.userAgent);
    window.open(storeURL);
}

function ResponsiveDrawer(props) {
    const { window } = props;
    const classes = useStyles();
    const theme = useTheme();
    const [mobileOpen, setMobileOpen] = React.useState(false);
    const [isOnline] = useState(navigator.onLine);

    const { t } = useTranslation();

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const handleDrawerClose = () => {
        setMobileOpen(false)
    }

    const openSignIn = () => {
        new Request(Messages.Auth.OpenSignIn, {}).sendRequest();
    }

    const drawer = (
        <div>
            <div className={classes.toolbar} />
            <Divider />
            <div onClick={handleDrawerClose} onKeyDown={handleDrawerClose} >
                <Menu setLoggedIn={props.setLoggedIn} isAdmin={props.isAdmin} isLoggedIn={props.isLoggedIn} />
            </div>
        </div>
    );

    const container = window !== undefined ? () => window().document.body : undefined;

    return (
        <div className={classes.root}>
            <CssBaseline />
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={handleDrawerToggle}
                        className={classes.menuButton}
                    >
                        <MenuIcon />
                    </IconButton>
                    <IconButton className={classes.logoButton} edge="start" color="inherit" aria-label="menu" onClick={() => { window.open("https://www.super-agent.com") }}>
                        <img src={Logo} className={classes.logoImage} alt="Super Agent" />
                    </IconButton>
                    <div className={classes.toolbarButtons}>
                        <Typography 
                            variant="caption" 
                            sx={{
                                marginRight: 5,
                                display: {
                                    xs: 'none',
                                    sm: 'inline'
                                }
                            }}
                        >
                            {props.isLoggedIn ? props.email : t('options.appbar.localstorage')}
                        </Typography>
                        <Button
                            className={classes.rightNavigationButton}
                            variant="contained"
                            color="secondary"
                            startIcon={props.isLoggedIn ? <ExitToAppIcon /> : <LockOpenIcon />}
                            onClick={props.isLoggedIn ? props.signOut : openSignIn}
                        >
                            {props.isLoggedIn ? t('options.menu.logout') : t('options.menu.signin')}
                        </Button>
                    </div>
                </Toolbar>
            </AppBar>
            <Router>
                <nav 
                    aria-label="Menu Items"
                    sx={{
                        display: {
                            xs: 'none'
                        }
                    }}
                >
                    <Hidden smUp implementation="css">
                        <Drawer
                            container={container}
                            variant="temporary"
                            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                            open={mobileOpen}
                            onClose={handleDrawerToggle}
                            classes={{
                                paper: classes.drawerPaper,
                            }}
                            ModalProps={{
                                keepMounted: true, // Better open performance on mobile.
                            }}
                        >
                            {drawer}
                        </Drawer>
                    </Hidden>
                </nav>
                <main className={classes.content}>
                    <div className={classes.toolbar} />
                    <Container>
                        { !isOnline ?
                            <NoInternetConnection />
                            :
                            <Grid container spacing={0}>
                                <Grid 
                                    item 
                                    xs={0} 
                                    sm={3} 
                                    sx={{
                                        display: {
                                            xs: 'none',
                                            sm: 'block'
                                        }
                                    }}
                                >
                                    <Menu setLoggedIn={props.setLoggedIn} isAdmin={props.isAdmin} isLoggedIn={props.isLoggedIn} />
                                </Grid>
                                <Grid item xs={12} sm={9} className={classes.mainGrid}>
                                    <div>
                                        <Switch>
                                            <Route exact path={["/", "/options.html"]}>
                                                <Dashboard isLoggedIn={props.isLoggedIn} signOutTrigger={props.signOutTrigger} setSignOutTrigger={props.setSignOutTrigger} />
                                            </Route>
                                            <Route path="/account">
                                                {props.isLoggedIn ? <Account /> : null }
                                            </Route>
                                            <Route path="/preferences">
                                                <CookiePreferences isLoggedIn={props.isLoggedIn} />
                                            </Route>
                                            <Route path="/settings">
                                                <BehaviorSettings isLoggedIn={props.isLoggedIn} isAdmin={props.isAdmin} />
                                            </Route>
                                            <Route path="/reports">
                                                <Reports isLoggedIn={props.isLoggedIn} />
                                            </Route>
                                            <Route path="/whitelist">
                                                <WhiteList />
                                            </Route>
                                        </Switch>
                                    </div>
                                </Grid>
                            </Grid>
                        }
                    </Container>
                </main>
            </Router>
            <AppBar position="fixed" className={classes.bottomAppBar}>
                <Toolbar>
                    <Typography>{t('options.dashboard.rate_me.title')}</Typography>
                    <Button className={classes.rating}
                        variant="outlined"
                        color="secondary"
                        startIcon={<ThumbUpIcon />}
                        onClick={onRatingClick}>
                        {t('options.dashboard.rate_me.button')}
                    </Button>
                </Toolbar>
            </AppBar>
        </div>
    );
}

export default ResponsiveDrawer;