// Material UI
import Grid from '@mui/material/Grid';
import CircularProgress from '@mui/material/CircularProgress';
import Switch from '@mui/material/Switch';
import FormControlLabel from '@mui/material/FormControlLabel';
import Tooltip from '@mui/material/Tooltip';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

// Icons
import IconButton from '@mui/material/IconButton';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';

// Styles
import { makeStyles } from '@mui/styles';

// Owned
import { RatingHelper } from '../../../../shared/RatingHelper';
import DataCard from '../../../Shared/View/DataCard';
import LogTable from './LogTable';
import { Request, Messages } from '../../../../shared';

// External
import { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '25ch',
        },
        textAlign: "center"
    },
    grid: {
        marginTop: '10px'
    },
    paper: {
        margin: theme.spacing(1)
    },
    paperContent: {
        margin: theme.spacing(2)
    },
    submit: {
        textAlign: "center"
    },
    info: {
        width: '100%',
        marginTop: '30px'
    },
    infoItem: {
        height: "40px",
        top: "50%",
        transform: "translate(0, 50%)"
    },
    switchTooltip: {
        display: 'none'
    },
    message: {
        [theme.breakpoints.down('xs')]: {
            textAlign: 'center',
            display: 'block'
        }
    },
    consentTrailContent: {
        display: 'flex',
        justifyContent: 'flex-end',
        width: '100%',
        padding: theme.spacing(1)
    },
    bottomToolbar: {
        width: '100%',
        backgroundColor: theme.palette.primary.main,
        alignItems: 'center',
        textAlign: "center",
        marginTop: '20px',
        borderRadius: '8px',
        [theme.breakpoints.up('md')]: {
            display: 'none'
        }
    },
    toolbarContent: {
        width: '100%',
        backgroundColor: theme.palette.primary.main,
        alignItems: 'center',
        textAlign: "center",
        padding: "5px"
    },
    ratingParagraph: {
        display: "inline-flex",
        color: "white",
        margin: "5px",
        marginRight: "5px"
    },
    rating: {
        margin: "5px",
        marginLeft: "5px",
    }
}));

function Logs(props) {
    const classes = useStyles();
    const { t } = useTranslation();

    const [loading, setLoading] = useState(true);
    const [logs, setLogs] = useState([]);
    const [consentTrailState, setConsentTrailState] = useState(false);

    const [data, setData] = useState({
        websitesVisited: 0,
        totalClicks: 0,
        timeSaved: 0
    });

    const loadLogs = () => {
        new Request(Messages.Logger.GetLogs).sendRequest()
            .then((results) => {
                const logs = results.message.payload.logs;
                // Remove logs without a sort key (logs that are only in cache) as we can't delete these
                const filteredLogs = logs.filter((log) => { return log.sk != null });

                setLogs(filteredLogs);
            })
            .catch((e) => {

            });
    }

    const loadUsageMetrics = () => {
        new Request(Messages.Logger.GetUsageMetrics).sendRequest()
            .then((results) => {
                const metrics = results.message.payload.metrics;
                setData({
                    websitesVisited: metrics.ws,
                    totalClicks: metrics.cc,
                    timeSaved: metrics.ms
                });
                setLoading(false);
            })
            .catch((e) => {

            });
    }

    const loadPreferences = () => {
        new Request(Messages.Logger.GetLoggingPreferences).sendRequest()
        .then((results) => {
            const preferences = results.message.payload.preferences;
            let logging = preferences.enabled;

            setConsentTrailState(logging);
        }).catch((e) => {

        });
    }

    const onRatingClick = (event) => {
        let storeURL = RatingHelper.storeURL(navigator.userAgent);
        window.open(storeURL);
    }

    const init = () => {
        loadLogs();
        loadUsageMetrics();
        loadPreferences();
    }

    useEffect(() => {
        init();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
        if (props.signOutTrigger === true) {
            setLogs([]);
            setData({
                websitesVisited: 0,
                totalClicks: 0,
                timeSaved: 0
            });
            setConsentTrailState(false);
            props.setSignOutTrigger(false);
        }

        if (props.isLoggedIn) {
            init();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props]);

    const deleteLogs = async (logList) => {
        await new Request(Messages.Logger.DeleteLogs, { logList }).sendRequest();

        setLoading(true);
        setTimeout(() => {
            init();
        }, 3000);
    }

    const changeConsentTrailState = async (event) => {
        let state = event.target.checked;

        setConsentTrailState(state);

        if (state === false) {
            try {
                await new Request(Messages.Logger.DeleteAllLogs).sendRequest();
                setLogs([]);
                loadUsageMetrics();
            } catch(e) {

            }
        }

        try {
            await new Request(Messages.Logger.UpdatePreferences, { logging: state }).sendRequest();
        } catch(e) {

        }
    }

    if (loading) {
        return (
            <CircularProgress />
        )
    } else {
        return (
            <div className={classes.grid}>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={4}>
                        <DataCard
                            number={data.websitesVisited}
                            description={t('options.dashboard.summary.grid.websites_visited.description')}
                            info={t('options.dashboard.summary.grid.websites_visited.info')}
                        />
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <DataCard
                            number={data.totalClicks}
                            description={t('options.dashboard.summary.grid.total_clicks.description')}
                            info={t('options.dashboard.summary.grid.total_clicks.info')}
                        />
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <DataCard
                            number={data.timeSaved.toFixed(1)}
                            description={t('options.dashboard.summary.grid.time_saved.description')}
                            info={t('options.dashboard.summary.grid.time_saved.info')}
                        />
                    </Grid>

                    <div className={classes.consentTrailContent}>
                        <FormControlLabel
                            control={<Switch checked={consentTrailState} color="secondary" />}
                            label={
                                <div>
                                    <Tooltip className={classes.switchTooltip}
                                        title={
                                            <Typography variant="body2">
                                                {t('options.dashboard.switch.info')}
                                            </Typography>
                                        }>
                                        <IconButton aria-label="info">
                                            <InfoOutlinedIcon fontSize="small" />
                                        </IconButton>
                                    </Tooltip>
                                    <Typography variant="caption">
                                        {t('options.dashboard.switch.label')}
                                    </Typography>
                                </div>
                            }
                            labelPlacement="start"
                            onChange={changeConsentTrailState}
                        />
                    </div>

                    {consentTrailState ?
                        <div className={classes.info}>
                            <Grid container spacing={1} justify="center" direction="row">
                                <Grid item>
                                    <InfoOutlinedIcon fontSize="small" />
                                </Grid>
                                <Grid item>
                                    <Typography variant="caption" className={classes.message}>
                                        {logs.length > 0 ?
                                            props.isLoggedIn ? t('options.dashboard.logs.table.delay_info') : t('options.dashboard.logs.table.no_login')
                                            :
                                            props.isLoggedIn ? t('options.dashboard.logs.table.no_logs') : t('options.dashboard.logs.table.no_login_no_logs')
                                        }
                                    </Typography>
                                </Grid>
                            </Grid>
                            <Grid item xs={12} md={12}>
                                <LogTable data={logs} deleteLogs={deleteLogs} />
                            </Grid>
                        </div>
                        :
                        <div className={classes.info}>
                            <Grid container spacing={1} justify="center" direction="row">
                                <Grid item>
                                    <InfoOutlinedIcon fontSize="small" />
                                </Grid>
                                <Grid item>
                                    <Typography variant="caption" className={classes.message}>
                                        {t('options.dashboard.logs.table.consent_trail_disable')}
                                    </Typography>
                                </Grid>
                            </Grid>
                        </div>
                    }

                </Grid>
                <Toolbar className={classes.bottomToolbar}>
                    <div className={classes.toolbarContent}>
                    <Typography className={classes.ratingParagraph}>
                        {t('options.dashboard.rate_me.title')}
                    </Typography>
                    <Button className={classes.rating}
                        variant="outlined"
                        color="secondary"
                        startIcon={<ThumbUpIcon />} 
                        onClick={onRatingClick}>
                        {t('options.dashboard.rate_me.button')}
                    </Button>
                    </div>
                </Toolbar>
            </div>
        );
    }
}

export default Logs;
