// External
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

// Material UI
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';

// Confirmation Dialog
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

// Alerts
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';
  
export default function AccountView(props) {
    const { t } = useTranslation();

    const [confirmationDialogOpen, setConfirmationDialogOpen] = useState(false);

    if (props.working) {
        return(
            <CircularProgress />
        )
    }

    const handleClose = (deleteAccount) => {
        setConfirmationDialogOpen(false);
        if (deleteAccount) {
            props.handleDeleteAccount();
        }
    }

    return(
        <div>
            <Grid container spacing={3}>
                <Grid item xs={12}>
                    <Paper elevation={3}>
                        <Box 
                            sx={{
                                padding: 2,
                            }}
                        >
                            <Typography variant='h6'>
                                {t('options.account.change_password.title')}
                            </Typography>
                            <Container maxWidth="xs">
                                <form noValidate onSubmit={props.handleSubmit}>
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        id="oldPassword"
                                        label={t('options.account.change_password.old_password')}
                                        name="oldPassword"
                                        type="password"
                                        autoFocus
                                        value={props.passwords.oldPassword}
                                        onChange={props.handlePasswordsChange}
                                    />
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        id="newPassword"
                                        label={t('options.account.change_password.new_password')}
                                        name="newPassword"
                                        type="password"
                                        value={props.passwords.newPassword}
                                        onChange={props.handlePasswordsChange}
                                    />
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        id="confirmPassword"
                                        label={t('options.account.change_password.confirm_password')}
                                        name="confirmPassword"
                                        type="password"
                                        value={props.passwords.confirmPassword}
                                        onChange={props.handlePasswordsChange}
                                    />
                                    <Button
                                        id="submit"
                                        type="submit"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        sx={{marginTop: 2}}
                                    >
                                        {t('options.account.change_password.title')}
                                    </Button>
                            
                                </form>
                            </Container>
                        </Box>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper elevation={3}>
                        <Box 
                            sx={{
                                padding: 2,
                            }}
                        >
                            <Typography variant='h6'>
                                {t('options.account.delete_account.title')}
                            </Typography>
                            <Typography variant='body'>
                                {t('options.account.delete_account.description')}
                            </Typography>
                            <Container maxWidth="xs">
                                    <Button
                                        id="deleteAccount"
                                        fullWidth
                                        variant="contained"
                                        color="error"
                                        onClick={() => { setConfirmationDialogOpen(true) }}
                                        sx={{marginTop: 2}}
                                    >
                                        {t('options.account.delete_account.title')}
                                    </Button>
                            </Container>
                        </Box>
                    </Paper>
                </Grid>
            </Grid>

            <Dialog
                open={confirmationDialogOpen}
                onClose={() => { handleClose(false); }}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {t('options.account.confirmation_dialog.title')}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                    {t('options.account.confirmation_dialog.description')}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => { handleClose(false); } }>
                        {t('options.account.confirmation_dialog.cancel')}
                    </Button>
                    <Button onClick={() => { handleClose(true); }}>
                        {t('options.account.confirmation_dialog.confirm')}
                    </Button>
                </DialogActions>
            </Dialog>

            <Snackbar 
                open={props.alert.showAlert} 
                autoHideDuration={2000} 
                onClose={() => { props.hideAlert() }}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center'
                }}
            >
                <Alert severity={props.alert.severity}>{props.alert.message}</Alert>
            </Snackbar>
        </div>
    )
}
