// React
import { useState } from 'react';

// Material UI
import { makeStyles } from '@mui/styles';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormHelperText from '@mui/material/FormHelperText';
import Switch from '@mui/material/Switch';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';

// External
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  cookieContent: {
    margin: theme.spacing(2)
  },
  paper: {
    margin: theme.spacing(1),
    maxWidth: '900px'
  },
  preferencesForm: {
    width: '100%'
  }
}));

function Preference(props) {
    const classes = useStyles();
    const [showMore, setShowMore] = useState(false);
  
    const { t } = useTranslation();
  
    return(
      <Paper elevation={3} className={classes.paper}>
        <div className={classes.cookieContent}>
          <Typography variant='h6'>
            {props.title}
          </Typography>
          <FormHelperText>
            {showMore ?
              <div>
                <Typography variant="body1" color="textPrimary">
                  {props.description}
                </Typography> 
                <Link href="#" onClick={() => { setShowMore(false) }}>
                  {t('general.show_less')}
                </Link>
              </div>
              :
              <div>
                <Typography variant="body1" color="textPrimary">
                  {props.placeholder}
                </Typography>
                <Link href="#" onClick={() => { setShowMore(true) }}>
                  {t('general.show_more')}
                </Link>
              </div> 
            }
          </FormHelperText>
          <FormControlLabel
            control={<Switch checked={props.checked} onChange={props.handleChange} name={props.name} color="secondary" />}
            label={props.label}
          />
        </div>
      </Paper>
    )
  }
  
  export default Preference;