// Material UI
import { makeStyles } from '@mui/styles';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';

// External
import React from 'react';
import { useTranslation } from 'react-i18next';

// Owned
import { Request, Messages } from '../../../../shared';

const useStyles = makeStyles((theme) => ({
    root: {
        verticalAlign: "center",
        marginTop: '10px'
    },
    message: {
        [theme.breakpoints.down('xs')]: {
            textAlign: 'center',
            display: 'block'
        }
    }
}));

function NoLogin() {
    const classes = useStyles();

    const { t } = useTranslation();

    const openSignIn = () => {
        new Request(Messages.Auth.OpenSignIn, {}).sendRequest();
    }

    const openSignUp = () => {
        new Request(Messages.Auth.openSignUp, {}).sendRequest();
    }

    return(
        <div className={classes.root}>
            <Grid container
                  spacing={3}
                  direction="column"
                  alignItems="center"
                  justify="center"
                  style={{ marginTop: '100px' }}
            >
                <Grid item xs={12}>
                    <Typography className={classes.message}>
                        {t('options.reports.no_login.body')}
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <Button color="primary" variant="contained" onClick={() => { openSignUp() }}>
                        {t('options.reports.no_login.button.signup')}
                    </Button>
                </Grid>
                <Grid item xs={12}>
                    <Link href="#" underline="always" onClick={() => { openSignIn() }}>
                        {t('options.reports.no_login.button.signin')}
                    </Link>
                </Grid>
            </Grid>
        </div>
    )
}

export default NoLogin;