import React, { useState, useEffect, useCallback } from 'react';

import { CircularProgress } from '@mui/material';

import CookiePreferencesView from './Views/CookiePreferencesView';
import BehaviorSettingsView from './Views/BehaviorSettingsView';

import { Request, Messages } from '../../../../shared';

// Localizations
import i18n from '../../../../i18n';

// HOCs cannot use hooks, so we abstract PreferencesWrapper with withPreferences
function withPreferences(Component, props) {
    function PreferencesWrapper(props) {
        const defaultLanguage = "en"

        const [currentLanguage, setCurrentLanguage] = useState(defaultLanguage);
        const [loading, setLoading] = useState(true);
        const [preferencesSaved, setPreferencesSaved] = useState(false);
        const [preferences, setPreferences] = useState({
            advertising: false,
            functional: true,
            performance: true,
            other: false,
            notifications: false,
            background: false,
            enableFilters: true,
            showUpdateNotifications: true
        });

        const saveUserPreferences = async (userPreferences) => {
            await new Request(
              Messages.UserPreferences.SetUserPreferences, 
              { userPreferences: userPreferences }
            ).sendRequest();
          }

        const handleLanguageChange = (event) => {
            let language = event.target.value;

            setCurrentLanguage(language);
            i18n.changeLanguage(language);
        };

        const initsCurrentLanguage = () => {
            let configuredLanguage = undefined;

            i18n.languages.some(function (language) {
            let hasResource = i18n.hasResourceBundle(language, 'translation')
            configuredLanguage = language;

            return hasResource;
            });

            if (configuredLanguage) {
            setCurrentLanguage(configuredLanguage);
            } else {
            setCurrentLanguage(defaultLanguage);
            }
        }

        // Listen to real time preference changes
        const setupConnection = async () => {
            try {
                var port = chrome.runtime.connect({ name: "preferencesListener" });
        
                port.onMessage.addListener(function (msg) {
                  switch (msg.type) {
                    case 'PREFERENCES_UPDATED':
                        setPreferences(msg.preferences);
                        break;
                    default:
                        break;
                  }
                });
                
                await new Request(Messages.DataSync.SetupDataSync, {}).sendRequest();
            } catch (e) {
                
            }
        }

        const getPreferences = useCallback(() => {
            new Request(Messages.UserPreferences.GetUserPreferences).sendRequest()
            .then(async (results) => {
                const userPreferences = results.message.payload.userPreferences;
                
                setLoading(false);
                setPreferences(userPreferences);
                });
        }, []);

        useEffect(() => {
            async function setup() {
                if (props.isLoggedIn) {
                    await setupConnection();
                }
            }

            initsCurrentLanguage();
            getPreferences();
            setup();
            
            return async function cleanup() {
                try {
                    await new Request(Messages.DataSync.StopDataSync).sendRequest();
                } catch(e) {

                }
            }
        }, [props.isLoggedIn, getPreferences]);

        const handleChange = (event) => {
            setPreferences({ ...preferences, [event.target.name]: event.target.checked });
            saveUserPreferences({ ...preferences, [event.target.name]: event.target.checked });
            setPreferencesSaved(true);
        };

        if (loading) {
            return <CircularProgress />
        } 

        const Component = props.component;

        return (
            <Component 
                currentLanguage={currentLanguage}
                handleLanguageChange={handleLanguageChange}
                preferences={preferences}
                handleChange={handleChange}
                preferencesSaved={preferencesSaved}
                setPreferencesSaved={setPreferencesSaved}
                isLoggedIn={props.isLoggedIn}
                isAdmin={props.isAdmin}
            />
        )
    }

    return <PreferencesWrapper 
            component={Component}
            isLoggedIn={props.isLoggedIn}
            isAdmin={props.isAdmin}
        />;
}

export const CookiePreferences = (props) => {
    return(
        withPreferences(CookiePreferencesView, props)
    )
}

export const BehaviorSettings = (props) => {
    return(
        withPreferences(BehaviorSettingsView, props)
    )
}
