// Material UI
import CircularProgress from '@mui/material/CircularProgress';

// Styles
import { ThemeProvider } from '@mui/material/styles';

// Owned
import './App.css';
import { Request, Messages } from '../../shared';
import * as Constants from '../Shared/Constants'
import GroupValidator from '../Shared/GroupValidator';
import ResponsiveDrawer from './Components/ResponsiveDrawer';
import { theme } from './theme';

// External
import React, { useEffect, useState } from 'react';

function App() {
  const [loggedIn, setLoggedIn] = useState(Constants.sessionState.LOADING);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [signOutTrigger, setSignOutTrigger] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [email, setEmail] = useState("");
 
  const signOut = (event) => {
    event.preventDefault();

    new Request(Messages.Auth.SignOut).sendRequest()
    .then(() => {
      setSignOutTrigger(true);
      setLoggedIn(Constants.sessionState.NOT_LOGGED_IN);
      setIsLoggedIn(false);
    })
    .catch(() => {

    });
  }

  const initAuth = () => {
    if (window.navigator.onLine) {
      new Request(Messages.Auth.CheckAuth, {}).sendRequest()
        .then((result) => {
          const user = result.message.payload;
          setEmail(user.attributes.email);
          setIsAdmin(GroupValidator.isUserAdmin(user));
          setLoggedIn(Constants.sessionState.LOGGED_IN);
          setIsLoggedIn(true);
        })
        .catch((e) => {
          setLoggedIn(Constants.sessionState.NOT_LOGGED_IN);
          setIsLoggedIn(false);
        });
    } else {
      setLoggedIn(Constants.sessionState.NOT_LOGGED_IN);
      setIsLoggedIn(false);
    }
  }

  // Need to listen for auth state changes
  const setupConnection = () => {
    var port = chrome.runtime.connect({ name: "optionsAuthListener" });

    port.onMessage.addListener(function (msg) {
      switch (msg.type) {
        case 'VALIDATE_AUTH':
          initAuth();
          break;
        default:
          break;
      }
    });
  }

  useEffect(() => {
    initAuth();
    setupConnection();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  switch (loggedIn) {
    case Constants.sessionState.LOADING:
      return (
        <ThemeProvider theme={theme}>
          <div className="App">
            <CircularProgress />
          </div>
        </ThemeProvider>
      )
    default:
      return (
        <ThemeProvider theme={theme}>
          <ResponsiveDrawer 
          isLoggedIn={isLoggedIn} 
          email={email} 
          signOut={signOut} 
          isAdmin={isAdmin} 
          signOutTrigger={signOutTrigger} 
          setSignOutTrigger={setSignOutTrigger}/>
        </ThemeProvider>
      )
  }
}

export default App;
