// Material UI
import MaUTable from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Fab from '@mui/material/Fab';
import Checkbox from '@mui/material/Checkbox';
import { makeStyles } from '@mui/styles';

// Icons
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

// External
import React, { useState } from 'react';
import { useTable } from 'react-table';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  info: {
    height: '40px',
    display: "flex",
    flexDirection: "column"
  },
  fixedHeigthPaper: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    textAlign: 'center'
  },
  fab: {
    position: "fixed",
    bottom: theme.spacing(2),
    right: theme.spacing(3),
    marginBottom: 60
  },
  descriptionText: {
    color: '#999999'
  },
  desktopTable: {
    display: 'table'
  },
  mobileTable: {
    display: 'none'
  },
  [theme.breakpoints.down('sm')]: {
    desktopTable: {
      display: 'none'
    },
    mobileTable: {
      display: 'table',
      marginTop: '10px'
    }
  }
}));

function Table({
  columns,
  data,
  setLogSelected,
  rowSelected,
  setRowSelected,
  allSelected,
  setAllSelected,
}) {

  const classes = useStyles();

  // Use the state and functions returned from useTable to build your UI
  const { getTableProps, headerGroups, rows, prepareRow } = useTable({
    columns,
    data
  });

  const selectAll = (e) => {
    let newState = Array.from({ length: rows.length }, i => i = !allSelected)
    setRowSelected(newState);
    setAllSelected(!allSelected);
    setLogSelected(!allSelected);
  }

  const checkboxChange = (e) => {
    const i = e.target.id;

    let newState = [...rowSelected];
    newState[i] = !rowSelected[i];

    // Is anything selected in newState?
    // Don't forget that parts of the array can be empty, check currentValue inside reduce
    setLogSelected(newState.some(element => element));
    setRowSelected(newState);
  }

  // Render the UI for your table
  return (
    <div>
      <MaUTable {...getTableProps()} className={classes.desktopTable}>
        <TableHead>
          {headerGroups.map(headerGroup => (
            <TableRow {...headerGroup.getHeaderGroupProps()}>
              <TableCell padding="checkbox">
                <Checkbox
                  checked={allSelected}
                  onChange={selectAll}
                />
              </TableCell>
              {headerGroup.headers.map(column => (
                <TableCell {...column.getHeaderProps()}>
                  {column.render('Header')}
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableHead>
        <TableBody>
          {rows.map((row, i) => {
            prepareRow(row)
            return (
              <TableRow {...row.getRowProps()}>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={rowSelected[i] ? rowSelected[i] : false}
                    onChange={checkboxChange}
                    id={i}
                  />
                </TableCell>
                {row.cells.map(cell => {
                  return (
                    <TableCell {...cell.getCellProps()}>
                      {cell.render('Cell')}
                    </TableCell>
                  )
                })}
              </TableRow>
            )
          })}
        </TableBody>
      </MaUTable>

      <MaUTable {...getTableProps()} className={classes.mobileTable}>
        <TableBody>
          {rows.map((row, i) => {
            prepareRow(row)
            return (
              <TableRow {...row.getRowProps()}>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={rowSelected[i] ? rowSelected[i] : false}
                    onChange={checkboxChange}
                    id={i}
                  />
                </TableCell>
                <TableCell>
                  <span className={classes.descriptionText}>{row.values["date"]}  {row.values["time"]} </span><br></br>
                  <span>{row.values["hostname"]} <br></br> </span>
                  <span className={classes.descriptionText}>{row.values["action"]}  {row.values["totalClicks"]} Clicks</span>
                </TableCell>
              </TableRow>
            )
          })}
        </TableBody>
      </MaUTable>
    </div>
  )
}

function LogTable(props) {

  const classes = useStyles();
  const { t } = useTranslation();

  const [logSelected, setLogSelected] = useState(false);
  const [rowSelected, setRowSelected] = useState([]);
  const [allSelected, setAllSelected] = useState(false);

  const memoizedData = React.useMemo(() => props.data, [props.data])

  const columns = React.useCallback((t) =>
    [
      {
        Header: t('options.dashboard.logs.table.header.date'),
        accessor: "date"
      },
      {
        Header: t('options.dashboard.logs.table.header.time'),
        accessor: "time"
      },
      {
        Header: t('options.dashboard.logs.table.header.hostname'),
        accessor: "hostname"
      },
      {
        Header: t('options.dashboard.logs.table.header.action'),
        accessor: "action"
      },
      {
        Header: t('options.dashboard.logs.table.header.total_clicks'),
        accessor: "totalClicks"
      }
    ], []
  );

  const deleteLogs = () => {
    let logsToDelete = [];

    // eslint-disable-next-line
    rowSelected.map((selected, index) => {
      if (selected) {
        logsToDelete.push(props.data[index].sk);
      }
    });

    setRowSelected([]);
    setAllSelected(false);
    setLogSelected(false);

    props.deleteLogs(logsToDelete);
  }

  if (props.data.length > 0) {
    return (
      <div className={classes.root}>
        <Table
          columns={columns(t)}
          data={memoizedData}
          setLogSelected={setLogSelected}
          rowSelected={rowSelected}
          setRowSelected={setRowSelected}
          allSelected={allSelected}
          setAllSelected={setAllSelected}
        />
        {logSelected ?
          <Fab color="secondary" aria-label="Delete" className={classes.fab} onClick={deleteLogs}>
            <DeleteForeverIcon />
          </Fab>
          : null}
      </div>
    )
  } else {
    return null;
  }
}

export default LogTable;
