//@ts-check

export class LocalizedByHelper {

    /**
     * @param {string} language
     */
    static localizedBy(language) {

        switch (language) {
            case "de":
                return "Jens"
            default:
                return null
        }

    }

    static localizedByURL(language) {
        
        switch (language) {
            case "de":
                return "https://jens.marketing"
            default:
                return null
        }

    }

}