// External
import { useEffect, useState } from 'react';

// Material UI
import CircularProgress from '@mui/material/CircularProgress';

// Owned
import PasswordPrompt from './PasswordPrompt';
import Logs from './Logs';
import { Request, Messages } from '../../../../shared';

function Logging(props) {
  
  const [keys, setKeys] = useState( {} );
  const [haveDEK, setHaveDEK] = useState(false);
  const [isLoaded, setIsLoaded] = useState( false );


  const checkDEK = async () => {
    try {
      await new Request(Messages.KeyManager.CheckDEK).sendRequest();
      setHaveDEK(true);
    } catch(e) {
      setHaveDEK(false)
    }
  }

  useEffect(() => {
    const init = async () => {
      if (props.isLoggedIn) {
        try {
          const result = await new Request(Messages.KeyManager.GetUserKeys).sendRequest();
          const keys = result.message.payload.keys;
  
          await checkDEK();
          setKeys(keys);
  
          setIsLoaded(true);
        } catch(e) {
  
        }
      } else {
        // Don't really care about keys
        setIsLoaded(true);
      }
    }

    init();
  }, [props.isLoggedIn]);

  if (!props.isLoggedIn) {
    return (
      <div>
        <Logs />
      </div>
    )
  }

  if (isLoaded) {
      if (keys.edek) {
        if (haveDEK) {
          return (
            <div>
              <Logs isLoggedIn={props.isLoggedIn} signOutTrigger={props.signOutTrigger} setSignOutTrigger={props.setSignOutTrigger}/>
            </div>
          )
        } else {
          return (
            <div>
              <PasswordPrompt setHaveDEK={setHaveDEK} />
            </div>
          )
        }  
      } else {
        return (
          null
        );
      }
  } else {
    return (
      <CircularProgress />
    )
  }
}

export default Logging;
