// Material UI
import { makeStyles } from '@mui/styles';
import Grid from '@mui/material/Grid';
import CircularProgress from '@mui/material/CircularProgress';

// Owned
import ReportsTable from './ReportsTable';
import { Request, Messages } from '../../../../shared';
import DateFormat from '../../../Shared/DateFormat';

// External
import { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
    grid: {
        marginTop: '10px',
    }
}));

function ReportsList() {
    
    const classes = useStyles();
    const { t } = useTranslation();

    const [loading, setLoading] = useState(true);
    const [reportedSites, setReportedSites] = useState([]);
    
    const init = () => {
        new Request(Messages.Report.GetReportedSites).sendRequest()
        .then((results) => {
            const reportedSites = results.message.payload.reportedSites;
            const tableData = reportedSites.map(function(site) {    

                var finalDate = "";
                if (Date.parse(site.date)) {
                    finalDate = DateFormat.formatDate(new Date(site.date));
                } else {
                    finalDate = "-";
                }

                var finalState = "";
                if (site.validatedState === undefined) {
                    finalState = site.state ? t('options.reports.states.resolved') : t('options.reports.states.under_validation');
                } else {
                    finalState = t('options.reports.states.' + site.validatedState);
                }
                
                return { 
                    pk: site.pk, 
                    hostname: site.hostname, 
                    date: finalDate, 
                    state: finalState
                };
             })

            setReportedSites(tableData);
            setLoading(false);
        })
        .catch( (e) => {
            setReportedSites([]);
            setLoading(false);
        })
    }

    useEffect( () => {
        init();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    if (loading) {
        return(
            <CircularProgress />
        )
    } else {
        return(
            <div className={classes.grid}>
                <Grid container spacing={4}>
                    <Grid item xs={12} md={12}>
                        <ReportsTable data={reportedSites}/>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default ReportsList;
