// Material UI
import { makeStyles } from '@mui/styles';
import FormControl from '@mui/material/FormControl';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormHelperText from '@mui/material/FormHelperText';
import Switch from '@mui/material/Switch';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import CssBaseline from '@mui/material/CssBaseline';
import Link from '@mui/material/Link';

// Owned
import { LocalizedByHelper } from '../../../../../shared/LocalizedByHelper';
import Preference from './Preference';

// External
import React from 'react';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  cookieContent: {
    margin: theme.spacing(2)
  },
  paper: {
    margin: theme.spacing(1)
  },
  translatedBy: {
    marginTop: "5px"
  },
  translatedByLink: {
    textDecoration: "underline"
  }
}));

function BehaviorSettingsView(props) {

  const { t } = useTranslation();

  const classes = useStyles();

  return (
      <div>
        <CssBaseline />
        <FormControl component="fieldset" style={{ width: "100%" }}>
          <FormGroup>
            {props.isAdmin ?
              <Paper elevation={3} className={classes.paper}>
                <div className={classes.cookieContent}>
                  <FormControlLabel
                    control={<Switch checked={props.preferences.background} onChange={props.handleChange} name="background" color="secondary" />}
                    label={t('options.behaviour_settings.form.act_in_background.title')}
                  />
                  <FormHelperText>
                    <Typography variant="body1" color="textPrimary">
                      {t('options.behaviour_settings.form.act_in_background.description')}
                    </Typography>
                  </FormHelperText>
                </div>
              </Paper>
              : null}
            <Paper elevation={3} className={classes.paper}>
              <div className={classes.cookieContent}>
                <Typography variant='h6'>
                  {t('options.behaviour_settings.form.language.title')}
                </Typography>
                <FormControl className={classes.formControl}>
                  <Select value={props.currentLanguage} onChange={props.handleLanguageChange}>
                    <MenuItem value={"du"}>Dutch</MenuItem>
                    <MenuItem value={"en"}>English</MenuItem>
                    <MenuItem value={"fr"}>French</MenuItem>
                    <MenuItem value={"de"}>German</MenuItem>
                    <MenuItem value={"it"}>Italian</MenuItem>
                    <MenuItem value={"pt"}>Portuguese</MenuItem>
                    <MenuItem value={"ru"}>Russian</MenuItem>
                    <MenuItem value={"es"}>Spanish</MenuItem>
                  </Select>
                </FormControl>

                {LocalizedByHelper.localizedBy(props.currentLanguage) ?
                  <div className={classes.translatedBy}>
                    {t('options.behaviour_settings.localized_by')} <Link href={LocalizedByHelper.localizedByURL(props.currentLanguage)} variant="body2" className={classes.translatedByLink}>
                      {LocalizedByHelper.localizedBy(props.currentLanguage)}
                    </Link>
                  </div>
                  : null
                }

              </div>
            </Paper>
            <Preference 
              title={t('options.behaviour_settings.form.updateNotifications.title')}
              name="showUpdateNotifications"
              placeholder={t('options.behaviour_settings.form.updateNotifications.placeholder')}
              description={t('options.behaviour_settings.form.updateNotifications.description')}
              checked={props.preferences.showUpdateNotifications}
              handleChange={props.handleChange}
              label={props.preferences.showUpdateNotifications ? t('general.accept') : t('general.reject')}
            />
          </FormGroup>
        </FormControl>
        <div>
          <Snackbar open={props.preferencesSaved} autoHideDuration={2000} onClose={() => { props.setPreferencesSaved(false); }}>
            <Alert severity="success">{t('general.saved_preferences')}</Alert>
          </Snackbar>
        </div>
      </div>
  )
}

export default BehaviorSettingsView;
