// Material UI
import { makeStyles } from '@mui/styles';
import FormControl from '@mui/material/FormControl';
import FormGroup from '@mui/material/FormGroup';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';
import CircularProgress from '@mui/material/CircularProgress';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import { debounce } from '@mui/material';

// Owned
import { Request, Messages } from '../../../../shared';

// External
import React, { useEffect, useState, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  content: {
    margin: theme.spacing(2),
    whiteSpace: "pre-line"
  },
  paper: {
    margin: theme.spacing(1),
    marginTop: theme.spacing(2)
  },
  disabledHostTextfield: {
    width: "100%",
    marginTop: "20px"
  }
}));

function WhiteList(props) {

  const { t } = useTranslation();

  const classes = useStyles();

  const [loading, setLoading] = useState(true);
  const [hostnames, setHostnames] = useState("");
  const [hostnamesSaved, setHostnamesSaved] = useState(false);

  useEffect(() => {
    new Request(Messages.Whitelist.GetDisabledHostnames)
    .sendRequest()
    .then((results) => {
      const hostnames = results.message.payload.disabledHostnames;
      setLoading(false);
      setHostnames(hostnames.join("\n"))
    })
  }, []);

  const handleChange = (event) => {
    let newHostnames = event.target.value.split("\n");

    new Request(Messages.Whitelist.SaveDisabledHostnames, { disabledHostnames: newHostnames })
    .sendRequest()
    .then(() => {
      setHostnamesSaved(true);
    })
  };

  const debouncedEventHandler = useMemo(
    () => debounce(event => {
      handleChange(event)
    }, 500)
    , []);

  return (
    loading ? <CircularProgress /> :
      <div>
        <CssBaseline />
        <FormControl component="fieldset" style={{ width: "100%" }}>
          <FormGroup>
            <Typography variant="h4" color="textPrimary">
            { t('options.whitelist.title') }
            </Typography>
            <Paper elevation={3} className={classes.paper}>
              <div className={classes.content}>
                <Typography variant="body1" color="textPrimary">
                  { t('options.whitelist.info') }
                </Typography>
                <TextField
                  id="outlined-multiline-static"
                  label={ t('options.whitelist.textfield_title') }
                  multiline
                  rows={15}
                  variant="outlined"
                  defaultValue={hostnames}
                  className={classes.disabledHostTextfield}
                  onChange={debouncedEventHandler}
                />
              </div>
            </Paper>
          </FormGroup>
        </FormControl>
        <div>
          <Snackbar open={hostnamesSaved} autoHideDuration={2000} onClose={() => { setHostnamesSaved(false); }}>
            <Alert severity="success">{t('options.whitelist.snack_bar_success')}</Alert>
          </Snackbar>
        </div>
      </div>
  )
}

export default WhiteList;
