import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from "react-router-dom";

import AccountView from './View/AccountView';

import { Request, Messages } from '../../../../shared';
// This is used elsewhere, should be shared
import { AuthErrors } from './AuthErrors';


export default function Account() {
    const { t } = useTranslation();
    const history = useHistory();

    const [alert, setAlert] = useState({
        showAlert: false,
        severity: "success",
        message: ""
    });

    const [passwords, setPasswords] = useState({
        oldPassword: "",
        newPassword: "",
        confirmPassword: ""
    });

    const [working, setWorking] = useState(false);
    
    const hideAlert = () => {
        setAlert({
            ...alert,
            showAlert: false
        });
    }

    const handlePasswordsChange = (event) => {
        setPasswords({
            ...passwords,
            [event.target.name]: event.target.value
        });
    }
    
    const handleSubmit = async (event) => {
        event.preventDefault();
        if (passwords.newPassword !== passwords.confirmPassword) {
            setAlert({
                showAlert: true,
                severity: "error",
                message: t('options.account.alerts.password_mismatch')
            });
            return;
        } else {
            setWorking(true);
            try {
                await new Request(Messages.Auth.ChangeUserPassword, {
                    oldPassword: passwords.oldPassword,
                    newPassword: passwords.newPassword
                }).sendRequest();

                setAlert({
                    showAlert: true,
                    severity: "success",
                    message: t('options.account.alerts.password_changed')
                });

                setPasswords({
                    oldPassword: "",
                    newPassword: "",
                    confirmPassword: ""
                });

                setWorking(false);
            } catch(e) {
                switch (e.message.payload.code) {
                    case AuthErrors.INCORRECT_PASSWORD:
                        setAlert({
                            showAlert: true,
                            severity: "error",
                            message: t('options.account.alerts.incorrect_password')
                        });
                        break;
                    case AuthErrors.INVALID_PASSWORD:
                        setAlert({
                            showAlert: true,
                            severity: "error",
                            message: t('options.account.alerts.invalid_password')
                        });
                        break;
                    case AuthErrors.LIMIT_EXCEEDED: 
                        setAlert({
                            showAlert: true,
                            severity: "error",
                            message: t('options.account.alerts.limit_exceeded')
                        });
                        break;
                    default:
                        break;
                }
                setWorking(false);
            }
        }
    }
    
    const handleDeleteAccount = async () => {
        setWorking(true);
        try {
            await new Request(Messages.Auth.DeleteUser, {}).sendRequest();
            history.push("/options.html");
        } catch (e) {
            setAlert({
                showAlert: true,
                severity: "error",
                message: t('options.account.alerts.error_deleting')
            })
        }
        setWorking(false);
    }
    
    return(
        <AccountView 
            alert={alert}
            hideAlert={hideAlert}
            working={working}
            passwords={passwords} 
            handlePasswordsChange={handlePasswordsChange} 
            handleSubmit={handleSubmit}    
            handleDeleteAccount={handleDeleteAccount}
        />
    )
}